'use client';

import {
  Box,
  Card,
  CardContent,
  Typography,
  Dialog,
  IconButton,
  Grid,
} from '@mui/material';
import { Close } from '@mui/icons-material';
import { useState } from 'react';

interface Flower {
  name: string;
  img: string;
  description: string;
}

const flowers: Flower[] = [
  {
    name: 'Rose',
    img: '/images/rose.jpg',
    description:
      'A classic symbol of love and romance, known for its vibrant colors and sweet fragrance.',
  },
  {
    name: 'Tulip',
    img: '/images/tulip.jpg',
    description:
      'Bright and cheerful flowers that come in many colors, often associated with spring.',
  },
  {
    name: 'Lily',
    img: '/images/lily.jpg',
    description:
      'Elegant flowers with a pleasant fragrance, often used in weddings and celebrations.',
  },
  {
    name: 'Sunflower',
    img: '/images/sunflower.jpg',
    description:
      'Large, vibrant flowers that follow the sun and symbolize adoration and loyalty.',
  },
];

const MainContent = () => {
  const [selectedFlower, setSelectedFlower] = useState<Flower | null>(null);

  const handleOpen = (flower: Flower) => setSelectedFlower(flower);
  const handleClose = () => setSelectedFlower(null);

  return (
    <>
      <Box p={4}>
        <Typography variant="h4" textAlign="center" mb={4}>
          Our Flowers
        </Typography>

        <Grid container spacing={4} justifyContent="center">
          {flowers.map((flower) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={flower.name}>
              <Card
                sx={{
                  cursor: 'pointer',
                  transition: 'transform 0.3s',
                  '&:hover': {
                    transform: 'scale(1.05)',
                  },
                }}
                onClick={() => handleOpen(flower)}
              >
                <Box
                  component="img"
                  src={flower.img}
                  alt={flower.name}
                  loading="lazy"
                  onError={(e) =>
                    (e.currentTarget.src = '/images/fallback.jpg')
                  }
                  sx={{
                    width: '100%',
                    height: 200,
                    objectFit: 'cover',
                  }}
                />
                <CardContent>
                  <Typography variant="h6">{flower.name}</Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>

      <Dialog open={!!selectedFlower} onClose={handleClose} maxWidth="sm" fullWidth>
        {selectedFlower && (
          <Box position="relative">
            <IconButton
              onClick={handleClose}
              sx={{ position: 'absolute', top: 8, right: 8 }}
              aria-label="close"
            >
              <Close />
            </IconButton>

            <Box
              component="img"
              src={selectedFlower.img}
              alt={selectedFlower.name}
              loading="lazy"
              onError={(e) =>
                (e.currentTarget.src = '/images/fallback.jpg')
              }
              sx={{
                width: '100%',
                height: 300,
                objectFit: 'cover',
              }}
            />
            <Box p={3}>
              <Typography variant="h5" gutterBottom>
                {selectedFlower.name}
              </Typography>
              <Typography variant="body1">
                {selectedFlower.description}
              </Typography>
            </Box>
          </Box>
        )}
      </Dialog>
    </>
  );
};

export default MainContent;
